export default{
  data() {
    return {
      form: {
        oldpwd: '',
        newpwd: ''
      }
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    changeAction() {
      this.$http({
        url: this.$api.changepwd,
        method: 'POST',
        data: this.form,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            uni.showToast({
              title: '修改密码成功，请重新登录',
              icon: 'none'
            })
            setTimeout(function() {
              uni.reLaunch({
                url: '../login/login'
              })  
            }, 1000)
          } else if(res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '修改密码失败',
              icon: 'none'
            })
          }
        }
      })
    }
  }
}
